.onboarding_wrapper{
    padding: 24px;
    width: 100%;
}

.onboard_main {
    width: 100%;
    height: calc(100vh - 93px);
    background: #fff;
    box-shadow: 0px 0px 4px rgba(0,0,0,0.1);
    border-radius: 8px;
}

.onboardTab_header {
    position: relative;
}

.onboarding_btn {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    padding: 8px 23px;
    border: 1px solid #BABFC3;
    border-radius: 4px;
    background: transparent;
    color: #6D7175;
    position: absolute;
    right: 14px;
    top: 10px;
}

.onboardTab_header .nav.nav-tabs {
    justify-content: center;
    border-bottom: 1px solid #E7E5E4;
    padding: 0 12px;    
}

.onboardTab_header .nav.nav-tabs .nav-item {
    margin-right: 0;
    display: flex;
    align-items: center;
}

.onboardTab_header .nav.nav-tabs .nav-item .nav-link {
    padding: 16px 20px;
    display: flex;
    align-items: center;
}

.obStepNumber {
    background:#BABFC3;
    border-radius: 100%;
    color:#fff;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    margin-right: 10px;
}

.obStepLabel {
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    color: #6D7175;
    margin-bottom: 0;
}

.onboardTab_header .nav.nav-tabs .nav-item .nav-link.active .obStepNumber{
    background: #202223;
    color:#fff;
}

.onboardTab_header .nav.nav-tabs .nav-item .nav-link.active .obStepLabel {
    color: #202223;
}

.onboardTab_header .nav.nav-tabs .nav-item .nav-link.completed .obStepNumber {
    background: #4e69ed;
}

.onboardTab_header .nav.nav-tabs .nav-item > img {
    margin: 0 16px;
    width: auto;
}

.onboardTab_tabsContent {
    height: calc(100vh - 153px);
    width: 100%;
}

.onboardTab_tabsContent .tab-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    height: calc(100% - 53px);
}

.onboardTab_tabsContent .tab-content .tab-pane {
    padding: 15px;
    height: 100%;
    width: 100%;
}

.onboardTab_tabsContent .tab-content .tab-pane .tabPane_inner {
    height: 100%;
    overflow-y: auto;
}

.onboarding_addFeed {
    display: flex;
    align-items: center;
    height: 100%;
}

.onboarding_addFeed .small_container {
    max-width: 900px;
    margin: 0 auto;
}

.obNetworkSubtext {
    font-size: 12px;
    color: #6D7175;
    font-weight: 500;
} 

.obSourceNetworks {
    position: relative;
    list-style: none;
    display: block;
    margin: 0 auto;
    text-align: center;
    width: 100%;
    padding: 10px 0;
}

.obSourceNetworks li {
    display: none;
    text-align: center;
    padding: 8px;
    vertical-align: middle;
    cursor: pointer;
}

.obSourceNetworks.allNetworks li{
    display: inline-block;
}

.obSourceNetworks li:nth-child(1), 
.obSourceNetworks li:nth-child(2), 
.obSourceNetworks li:nth-child(3), 
.obSourceNetworks li:nth-child(4), 
.obSourceNetworks li:nth-child(5){
    display: inline-block;
}

.obSourceNetworks li a {
    position: relative;
    display: block;
    background-color: #fff;
    border-radius: 4px;
    padding: 5px 18px;
    text-decoration: none;
    border: 1px solid #BABFC3;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.05);
    transition: border 500ms ease-out;
}

.obSourceNetworks li a img {
    width: 20px;
    display: inline-block;
}

.obSourceNetworks li a span {
    display: inline-block;
    margin-left: 8px;
    color: #202223;
    font-weight: 500;
    line-height: 16px;
    font-size: 12px;
}

.obSourceNetworks .newBadge_addfeed {
    position: absolute;
    background: #4969ED;
    top: -12px;
    right: -16px;
    color: #fff !important;
    padding: 5px;
    font-size: 10px!important;
    font-weight: 700 !important;
    border-radius: 4px;
    border: 2px solid #f2f6f8;
    line-height: 7px !important;
    display: block;
}

.obAddFeedHead{
    margin-bottom: 5px;
}

.obNetworkTitle {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 8px;
}

.obNetworkSubtext{
    font-size: 13px;
    font-weight: 500;
    color: #6D7175;
    margin-bottom: 0;
}

.showAllNetworks_btn {
    font-size: 14px;
    font-weight: 500;
    color: #4E69ED;
    border: none;
    background: transparent;
    padding: 10px 15px;
    margin: 0 auto;
    text-align: center;
    display: flex;
    align-items: center;
}

.showAllNetworks_btn svg {
    margin-right: 5px;
}

.showAllNetworks_btn:hover {
    color: #5354FC;
}

.showAllNetworks_btn:hover svg path {
    fill: #5354FC;
}

.addfeedContainer {
    max-width: 790px;
    margin: 0 auto;
}

.addfeedContainer .card.card-custom {
    background: transparent;
    box-shadow: none;
    padding-left: 0;
    padding-right: 0;
}

.obSyncProduct_tooltip.obAddFeed_tooltip {
    margin-top: 5px;
}

.obSyncProduct_tooltip.obAddFeed_tooltip {
    background: #EAF5FF;
}

.obSyncProduct_tooltip.obAddFeed_tooltip .obInfobox {
    background: #C9E5FF;
}

.obCreateFeed_bottomAction {
    margin-top: 50px;
}

.obcreateFeed_bacnBtn{
    background: transparent;
    border: none;
    outline: 0;
    color: #6D7175;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
}

.obcreateFeed_bacnBtn svg{
    margin-right: 13px;
}

.obcreateFeed_bacnBtn svg path{
    fill: #6D7175;
}

.obSupportBottom {
    background: #F2F5FF;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 15px;
}

.obSupportText{
    color: #202223;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 0;
    margin-right: 28px;
}

.obSupport_btns {
    display: flex;
    align-items: center;
}

.obSupport_btns button, .obSupport_btns a {
    border: 1px solid #4E69ED;
    background: #F2F5FF;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    padding: 6px 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    color: #000;
}

.obSupport_btns button:first-child{
    margin-right: 10px;
}

.obSupport_btns button svg, .obSupport_btns button, .obSupport_btns a svg {
    margin-right: 5px;
}

.obSyncProduct_container {
    max-width: 750px;
    margin: 0 auto;
}

.syncProduct_wrap {
    /* height: 100%;
    display: flex;
    align-items: center; */
    margin-top: 65px;
}

.obSyncProduct_container {
    max-width: 830px;
    margin: 0 auto;
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
    flex-wrap: wrap;
}

.obSyncProduct_heading {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 12px;
    margin-bottom: 21px;
    border-bottom: 1px solid #E6ECEF;
}

.obSyncleftAlign h4 {
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    color: #202223;
    margin-bottom: 3px;
}

.obSyncleftAlign p{
    font-size: 14px;
    font-weight: 400;
    line-height: 23px;
    color: #6D7175;
    margin-bottom: 0;
}

.obImport_btn {
    font-size: 13px;
    font-weight: 600;
    color: #4E69ED;
}

.obImport_btn svg{
    margin-right: 5px;
}

.obSyncProduct_tabsContainer {
    display: block;
    width: 100%;
}

.obSyncProduct_tabsContainer .nav.nav-tabs {
    width: 100%;
}

.obSyncProduct_tabsContainer .nav.nav-tabs .nav-item {
    margin-right: 0;
}

.obSyncProduct_tabsContainer .nav.nav-tabs .nav-item .nav-link {
    padding: 8px 14px;
    border-bottom: 2px solid transparent;
    height: 46px;
}

.obSyncProduct_tabsContainer .nav.nav-tabs .nav-item .nav-link.active {
    border-bottom-color: #4E69ED;
}

.obSyncProduct_tabsContainer .nav.nav-tabs .nav-item .nav-link p {
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 600;
    color: #202223;
}

.obSyncProduct_tabsContainer .nav.nav-tabs .nav-item .nav-link p img {
    margin-right: 9px;
}

.onboardTab_tabsContent .obSyncProduct_tabsContainer .tab-pane {
    padding: 26px 0;
}

.obFormGroup-wrap{
    display: flex;
    align-items: flex-start;
}

.obFormGroup-wrap .form-group {
    width: calc(100% - 185px);
    margin-right: 15px;
}

.obFormGroup-wrap .form-group {
    width: calc(100% - 212px);
    margin-right: 22px;
    margin-bottom: 0;
}

.obFormGroup-wrap .form-group input {
    height: 41px;
    font-size: 13px;
    font-weight: 500;
}

.obSyncProduct_tooltip {
    padding: 15px;
    background: #FFFDEA;
    border-radius: 8px;
    margin-top: 50px;
} 

.obInfobox {
    background: #FFF490;
    display: inline-flex;
    padding: 5px;
    border-radius: 4px;
    color: #202223;
    align-items: center;
    justify-content: center;
    margin-bottom: 8px;
}

.obInfobox a {
    color: #4E69ED;
}

.obInfobox a:hover{
    color:#5354FC;
}

.obInfobox svg{
    margin-right: 7px;
}

.obSyncProduct_tooltip p{
    font-size: 13px;
    font-weight:500;
    color: #6D7175;
}

.obFeedCreated_Wrapper {
    max-width: 750px;
    margin: 100px auto 0;
    text-align: center;
}

.obFeedCreatedIcon {
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
}

.obFeedCreated_Wrapper h3 {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 12px;
}

.obFeedCreated_Wrapper p {
    font-size: 13px;
    font-weight: 500;
    color: #6D7175;
    margin-bottom: 22px;
}

.obFeedCreated_Wrapper p b{
    font-weight: 600;
    display: block;
}

.obProgressBar {
    margin: 3px 7px 0 0;
    cursor: pointer;
}

.header-fixed.aside-minimize.onboarding_active .header {
    left: 0;
}

.header-fixed.aside-minimize.onboarding_active .wrapper {
    padding-left: 0;
    padding-top: 45px;
}

.header-fixed.aside-minimize.onboarding_active .no_subheader{
    padding: 0;
}

.header-fixed.aside-minimize.onboarding_active #kt_content {
    height: calc(100vh - 45px);
}

@media screen and (max-width:1199px){
    .obProgressBar {
        margin: 12px 7px 0 0;
        cursor: pointer;
    }

    .header-fixed.aside-minimize.onboarding_active #kt_content, .onboard_main, .onboardTab_tabsContent, .onboardTab_tabsContent .tab-content  {
        height: auto;
    }
}

@media screen and (max-width:991px){
    .onboardTab_header .nav.nav-tabs{
        justify-content: flex-start;
    }

    .obSupportBottom {
        flex-wrap: wrap;
        padding: 15px;
    }

    .obSupportText {
        margin-bottom: 20px;
        text-align: center;
    }
}


@media screen and (max-width:767px){

    .onboardTab_header {
        padding-top: 50px;
    }

    .onboarding_wrapper {
        padding: 24px 15px;
    }

    .onboardTab_header .nav.nav-tabs .nav-item .nav-link{
        padding: 16px 10px;
    }

    .obStepNumber{
        margin-right: 6px;
    }

    .onboardTab_header .nav.nav-tabs .nav-item > img {
        margin: 0 10px;
    }

    .onboardTab_tabsContent .tab-content {
        height: auto;
    }

    .onboarding_addFeed,
    .onboardTab_tabsContent .tab-content .tab-pane .tabPane_inner,
    .onboardTab_tabsContent .tab-content .tab-pane,
    .onboardTab_tabsContent .tab-content,
    .onboardTab_tabsContent,
    .onboard_main{
        height: auto;
    }

    .obFeedCreated_Wrapper {
        margin: 50px auto;
    }

    .obSyncProduct_heading {
        flex-wrap: wrap;
    }

    .obSyncleftAlign {
        margin-bottom: 20px;
    }

    .syncProduct_wrap {
        margin-top: 15px;
    }
    

}

@media screen and (max-width:575px){

    .onboardTab_header .nav.nav-tabs .nav-item .nav-link {
        padding: 16px 4px;
    }

    .obStepNumber {
        width: 20px;
        height: 20px;
        font-size: 12px;
        line-height: normal;
    }

    .obStepLabel {
        font-size: 13px;
    }

    .onboardTab_header .nav.nav-tabs .nav-item > img {
        max-width: 7px;
    }
}

@media screen and (max-width:480px){
    .obStepLabel {
        font-size: 12px;
    }

    .onboardTab_header .nav.nav-tabs .nav-item > img {
        max-width: 6px;
        margin: 0 5px;
    }

    .showAllNetworks_btn {
        display: block;
    }

    .onboardTab_header .nav.nav-tabs .nav-item .nav-link {
        padding: 16px 10px;
    }

    
}

@media screen and (max-width:360px){
    .obSupport_btns {
        flex-wrap: wrap;
    }

    .obSyncProduct_tabsContainer .nav.nav-tabs .nav-item {
        margin-bottom: 15px;
    }

    .obSyncProduct_heading {
        flex-wrap: wrap;
    }

    .obImport_btn {
        margin-top: 15px;
    }

    .obFormGroup-wrap {
        flex-wrap: wrap;
    }

    .obFormGroup-wrap .form-group {
        width: 100%;
        margin-bottom: 20px;
        margin-right: 0;
    }

    .obSupport_btns button{
        margin-bottom: 20px;
    }
}