/* MULTICHANNEL PERFORMANCE CSS START */

.multichannel_performance {
    border-radius: 8px;
    background: #FFF;
    box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.20);
    padding: 20px 19px 0 19px;
    margin-bottom: 25px;
}

.multichannel_performance .section_heading{
    color: #333;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 20px;
    padding-bottom: 11px;
    border-bottom: 1px solid #E6ECEF;
    margin-bottom: 6px;
}

.multichannel_tabs.nav.nav-tabs .nav-link{
    padding: 16px 20px;
    font-size: 14px;
    font-weight:500;
    line-height: 17px;
}

.multichannel_tabs.nav.nav-tabs .nav-item:last-child {
    margin-right: 0;
}

.multichannel_tabs.nav.nav-tabs .nav-link.active{
    color: #5254FD !important;
    border-color: #5254FD;
}

.multichannel_performance .tab-content .viewDetails{
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    margin-right: 18px;
    background: none;
    color: #4e69ed;
    border: 0px;
    text-transform: capitalize;
}

.multichannel_performance .tab-content .dropdown-toggle {
    background: #fff;
    border: 1px solid #BABFC3;
    color: #333333;
    padding: 8px 55px 8px 20px;
    text-wrap: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 250px;
}

.multichannel_performance .tab-content .dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 0.5em solid;
    border-right: 0.5em solid transparent;
    border-bottom: 0;
    border-left: 0.5em solid transparent;
    top: 15px;
    position: absolute;
    right: 15px;
}

