.startingScreen {
    width: 100%;
    background: #fff;
    min-height: 100vh;
    position: relative;
    top: -15px;
    padding-top: 50px;
    padding-bottom: 90px;
}

.startScreen_inner .nav-tabs {
    border-bottom: none;
}

.screen_heading{
    text-align: center;
    margin-bottom: 24px;
}

.screen_heading h4 {
    color: #202223;
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 5px;
}

.screen_heading p {
    color: #6D7175;
    font-size: 13px;
    font-weight: 400;
}

.header_icon{
    margin-right: 18px;
}

.header_icon svg {
    max-width: 28px;
}

.header_number {
    width: 20px;
    height: 20px;
    border: 1px solid #4E69ED;
    border-radius: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: #4E69ED;
    font-size: 12px;
    margin-right: 18px;
}

.startingScreen .nav.nav-tabs .nav-item {
    width: 50%;
    margin: 0;
}

.startingScreen .nav.nav-tabs .nav-item .nav-link {
    width: 100%;
    border: 1px solid #DBE1E5;
    height: 100%;
    border-radius: 0;
    transition: all 0.3s ease-in-out;
}

.startingScreen .nav.nav-tabs .nav-item .nav-link.active{
    border-color: #BABFC3;
    border-bottom: #fff;
}

.startingScreen .nav.nav-tabs .nav-item:first-child .nav-link.active{
    border-right: #fff;
    border-radius: 8px 0 0 0;
}

.startingScreen .nav.nav-tabs .nav-item:last-child .nav-link.active{
    border-left: #fff;
    border-radius: 0 8px 0 0;
}

.startingScreen .tab-content {
    border: 1px solid #ccc;
    padding: 20px;
    border-top: none;
    border-radius: 0 0 8px 8px;
}

.startingScreen .accordion-body {
    padding: 24px 28px 19px;
}

.startingScreen .accordion-item {
    margin-bottom: 20px;
    border: 1px solid #DBE1E5 !important;
    border-radius: 6px;
}

.startingScreen .accordion-item:last-child{
    margin-bottom: 0;
}

.startingScreen .accordion .accordion-item .accordion-button {
    background: none !important;
    border-radius: 0px;
    box-shadow: none;
    border-bottom: 1px solid #DBE1E5;
    transition: all 0.3s ease-in-out;
}

.startingScreen .accordion .accordion-item .accordion-button.collapsed{
    border-bottom: 1px solid #DBE1E5;
}

.startingScreen .accordion-button:after {
    display: none;
}

.startingScreen .accordion .accordion-item .accordion-button .angel_icon svg {
    transform: rotate(-90deg);
    transition: all 0.3s ease-in-out;
}

.startingScreen .accordion .accordion-item .accordion-button.collapsed .angel_icon svg {
    transform: rotate(90deg);
    transition: all 0.3s ease-in-out;
}

.startingScreen .accordion-button .align_left, .startingScreen .accordion-button .align_right {
    width: 50%;
}

.startingScreen .accordion-button .align_left {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
}

.startingScreen .accordion-button .align_left .cgIcon {
    margin-right: 10px;
}

.startingScreen .accordion-button .align_right {
    display: flex;
    align-items: center;
    justify-content: end;
}

.startingScreen .accordion-button .align_right span {
    font-size: 12px;
    color: #6D7175;
    font-weight: 600;
    margin-right: 16px;
    position: relative;
}

.startingScreen .accordion-button .align_right p{
    font-size: 12px;
    color: #6D7175;
    font-weight: 400;
    position: relative;
}

.startingScreen .accordion-button .align_right p:after {
    position: absolute;
    content: "";
    background: #6D7175;
    width: 2px;
    height: 2px;
    bottom: 4px;
    left: -8px;
    border-radius: 100%;
}

.startingScreen .itemHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.startingScreen .itemHead .heading_left {
    color: #6D7175;
    font-size: 12px;
}

.markasDone {
    background: #F5F5F6;
    border: none;
    padding: 8px 16px;
    font-size: 12px;
    font-weight: 500;
    color: #6D7175;
    white-space: nowrap;
}

.markasDone .icon {
    margin-right: 5px;
}

.markasDone .icon svg {
    max-width: 18px;
    height: auto;
}

.steps .left_align{
    display: flex;
    align-items: center;
}

.steps .left_align p {
    color: #202223;
    font-size: 12px;
}

.steps {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #DBE1E5;
    padding: 8px 18px 8px 8px;
    border-radius: 6px;
    margin-bottom: 8px;
    position: relative;
    transition: all 0.3s ease-in-out;
}

.steps:after {
    position: absolute;
    content: "";
    width: 1px;
    height: 10px;
    bottom: -10px;
    left: 24px;
    border: 1px dotted #BABFC3;
}

.steps:last-child:after{
    display:none;
}

.steps.active:after {
    border-color: #6dc15a;
}

.steps:hover, .steps:focus, .steps:active {
    border-color: #4E69ED;
    box-shadow: 0 0 6px rgba(0,0,0,0.2);
}

.startingScreen .nav.nav-tabs .nav-item .nav-link:hover, 
.startingScreen .nav.nav-tabs .nav-item .nav-link:focus {
    box-shadow: 0 -3px 8px rgba(0,0,0,0.1);
}

.steps.active{
    border-color: #4E69ED;
}

.steps .left_align .step_icon {
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 3px;
    margin-right: 15px;
}

.startingScreen .accordion-header {
    position: relative;
}

.startingScreen .accordion-header .progress_bar {
    position: absolute;
    bottom: 0px;
    z-index: 9;
    background: #6DC15A;
    height: 2px;
}

.startingScreen .accordion-header .progress_bar.w-100{
    width: 100%;
}

.startingScreen .accordion-header .progress_bar.w-75{
    width: 75%;
}

.startingScreen .accordion-header .progress_bar.w-50{
    width: 50%;
}

.startingScreen .accordion-header .progress_bar.w-33{
    width: 33.3333%;
}

.startingScreen .accordion-header .progress_bar.w-25{
    width: 25%;
}

.steps.active .step_icon svg g path:nth-child(1) {
    fill: #82e56c;
}

.steps.active .step_icon svg g path:nth-child(2) {
    fill: #6dc15a;
}

.steps.active .left_align .step_icon {
    border-color: #6dc15a;
}

.strip_bottom {
    position: fixed; 
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    left: 76px;
    width: calc(100% - 76px);
    padding: 8px 15px;
    z-index: 9;
    border-top: 1px solid rgb(145 145 145 / 10%);
}

.strip_bottom  p{
    margin-bottom: 0;
    font-size: 12px;
}
.strip_bottom_up{ bottom: 40px;}
.strip_bottom_down{ bottom: 0px;}

.strip_bottom .btn.btn-primary {
    margin-left: 15px;
    margin-right: 80px;
}

.strip_bottom .btn.btn-outline-light{
    border: 1px solid #BABFC3;
    background: none;
}

.strip_bottom .btn.btn-outline-light:hover,
.strip_bottom .btn.btn-outline-light:focus,
.strip_bottom .btn.btn-outline-light:active{
    border-color:#202223;
    background: none;
}

.startingScreen .accordion .accordion-item .accordion-button:hover, 
.startingScreen .accordion .accordion-item .accordion-button:focus, 
.startingScreen .accordion .accordion-item .accordion-button.active {
    box-shadow: 0 0 3px rgba(0,0,0,0.3);
}

@media screen and (max-width:1300px){
    .strip_bottom .align_left {
        max-width: 50%;
    }
}

@media screen and (max-width:1199px){

    .startingScreen .itemHead .heading_left {
        width: calc(100% - 175px);
    }

    .steps .left_align {
        width: calc(100% - 28px);
    }

    .startingScreen{
        height: auto;
        padding-bottom: 0px;
    }

    .strip_bottom {
        left: 0;
        width: 100%;
        position: relative;
        margin-top: 20px;
        top: 0;
    }

    .strip_bottom .align_left {
        width: calc(100% - 300px);
    }

    .strip_bottom .btn.btn-primary {
        margin-right: 0px;
    }

}

@media screen and (max-width:991px){
    
}

@media screen and (max-width:767px){
    
    .startingScreen {
        padding-bottom: 20px;
    }

    
}

@media screen and (max-width:575px){

    .startingScreen {
        padding-top: 30px;
    }

    .startingScreen .itemHead {
        flex-wrap: wrap;
    }

    .startingScreen .itemHead .heading_left {
        width: 100%;
        margin-bottom: 15px;
    }

    .steps{
        margin-bottom: 20px;
    }

    .steps:after {
        height: 19px;
        bottom: -20px;
    }

    .header_icon{
        margin-right: 5px;
    }

    .startingScreen {
        height: auto;
        top: 0;
    }

    .strip_bottom {
        display: block;
        text-align: center;
        padding: 15px;
    }

    .strip_bottom .align_left, .strip_bottom .align_right {
        width: 100%;
    }

    .strip_bottom .align_right{ 
        margin-top: 20px;
    }

    .strip_bottom .align_left{
        max-width: 100%;
    }

}

@media screen and (max-width:460px){
    .startingScreen .nav.nav-tabs .nav-item {
        width: 100%;
    }

    .startingScreen .nav.nav-tabs .nav-item:first-child .nav-link.active {
        border-right: 1px solid #BABFC3;
        border-bottom: 1px solid #BABFC3;
    }

    .startingScreen .nav.nav-tabs .nav-item:last-child .nav-link.active {
        border-left: 1px solid #BABFC3;
        border-bottom: 1px solid #BABFC3;
    }

    .startingScreen .accordion .accordion-item .accordion-button {
        display: block;
    }

    .startingScreen .accordion-button .align_left {
        width: 100%;
        display: block;
        display: inline-flex;
    }

    .startingScreen .accordion-button .align_right{
        width:100%;
        justify-content: flex-start;
        margin-top: 15px;
    }

    .startingScreen .accordion-body {
        padding: 15px;
    }

    .steps .left_align {
        align-items: self-start;
    }

    .startingScreen .nav.nav-tabs .nav-item:nth-child(2) {
        margin: 20px 0;
    }

    .startingScreen .tab-content {
        border: 1px solid #ccc;
    }
}





